import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Chip from '@material-ui/core/Chip';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import Default from "../Default";

import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";
import logo from '../logo.svg';
import '../App.css';

const categories = [
    {id: 1001, label: "Jewelry and Wristwatches"},
    {id: 1002, label: "International Fashions"},
    {id: 1003, label: "Trendy Clothing"},
    {id: 1004, label: "Makeup and Skin Care"},
    {id: 1005, label: "Home Goods"},
    {id: 1006, label: "Food and Dining"},
    {id: 1007, label: "Culture and Creativity"},
];

const styles = {
    root: {
      display: 'flex',
      flexWrap: 'wrap',
      justifyContent: 'space-around',
      overflow: 'hidden',
      //backgroundColor: theme.palette.background.paper,
    },
    gridList: {
      width: "100%",
      height:"40%",
    },
  };

class BrandNavi extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
        currentTime: new Date().toString(), 
        selectedCategoryId: -1, 
        filteredBrandData: Default.getBrandData()
    }
  }

  getCurrentTime() {
    return this.state.currentTime;
  }

  onCategorySelected(category) {

    console.log(">> onCategorySelected: " , category);
    let filteredBrandData = [];
    let newCategoryId = category.id;
    if(category.id == this.state.selectedCategoryId) {
        filteredBrandData = Default.getBrandData();
        newCategoryId = -1;
    } else {
        Default.getBrandData().forEach(element => {
            if(element.categoryId == category.id) {
                filteredBrandData.push(element);
                console.log("push ", element);
            }
        });
    }

    this.setState({
        selectedCategoryId: newCategoryId, 
        filteredBrandData: filteredBrandData
    });
  }

  navigateToPathWithBrandInfo(path, brandInfo) {
    this.props.history.push(
        {
          pathname: path,
          state: { brandInfo: brandInfo }
        }
      );
  }

  render() {

      return (
      <div className="App">
        <div style={{display: "flex"}}>
            <div style={{backgroundColor: "rgb(255,204,205)", flex: 1}}>
                <h1>Brand Navigation</h1>
                <h3>{this.state.currentTime}</h3>
            </div>
            <div style={{backgroundColor: "rgb(206,155,186)"}}>
                <IconButton aria-label="Home">
                    <Link to="/">
                    <img src="./icons/home.svg" style={{width: "80%"}}/>
                    </Link>
                </IconButton>
            </div>
        </div>
        <div style={{margin: "20px"}}>
        {categories.map(category => (
            <Chip key={category.id}
                color={(this.state.selectedCategoryId==category.id)?"primary":"default"}
                onClick={this.onCategorySelected.bind(this, category)}
                label={category.label}
                style={{fontSize: "1.2rem", height: "5vh", margin: "5px"}}
            />
          ))}
        </div>
        <div style={{flex: 1}}>
        <GridList cellHeight={"25%"} style={styles.gridList} cols={3}>
          {this.state.filteredBrandData.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1}>
              <img onClick={this.navigateToPathWithBrandInfo.bind(this, "/brand", tile)}
                src={tile.img} alt={tile.title} style={{height: "100%", width: "100%"}}/>
            </GridListTile>
          ))}
          </GridList>
        </div>
      </div>
      );
  }
}

  export default BrandNavi;


