import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Icon from '@material-ui/core/Icon';
import PageviewIcon from '@material-ui/icons/Pageview';

import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';

import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import logo from '../logo.svg';
import '../App.css';

const DBG = false;

const currencies = [
  "精品服饰",
  "国际妆品", "休闲娱乐", "鞋包配饰", "生活服务", "运动户外", "儿童天地", "珠宝首饰", "餐饮美食", "数码电子", "家居生活"
];

class WebCommand extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      cmcode: this.getLastCMCode(), 
      url: this.getLastUrl(), 
      params: this.getLastParams(), 
      cmpath: this.getLastCMPath(), 
      webviewCmd: "", 
      targetUrl: "",
    }
  }

  setLastCMPath(value) {
    localStorage.cmpath = value;
  }
  getLastCMPath() {
    return localStorage.cmpath;
  }

  setLastCMCode(value) {
    localStorage.cmcode = value;
  }
  getLastCMCode() {
    return localStorage.cmcode;
  }

  setLastParams(value) {
    localStorage.params = value;
  }
  getLastParams() {
    if(localStorage.params == null) {
      localStorage.params = currencies[0];
    }
    return localStorage.params;
  }

  setLastUrl(url) {
    console.log(">> setLastUrl:", url);
    localStorage.cmurl = url;
  }

  getLastUrl() {
    return localStorage.cmurl;
  }

  handleChange = name => event => {

    let value = event.target.value;
    if(name == "url") {
      this.setLastUrl(value);
    } else if(name == "cmpath") {
      this.setLastCMPath(value);
    } else if(name == "params") {
      this.setLastParams(value);
    } else if(name == "cmcode") {
      this.setLastCMCode(value);
    }

    this.setState({
      [name]: value,
    });
  };

  onButtonCheckUrlClicked() {
    if (DBG) console.log(">> onButtonCheckUrlClicked");
    window.open(this.state.targetUrl, "_blank");
  }

  onButtonBuildClicked() {
    if (DBG) console.log(">> onButtonBuildClicked");
    let jsonString = "{" +
      "\"cmpath\":" + "\"" + this.state.cmpath +"\"," +
      "\"cmcode\":" + "\"" + this.state.cmcode +"\"," +
      "\"params\":" + "\"" + this.state.params +"\"," +
      "\"url\":" + "\"" + this.state.url +"\"" +
      "}";

    if (DBG) console.log(jsonString);

    let jsonObj = null;
    try {
      jsonObj = JSON.parse(jsonString);
      jsonString = "webview_cmd= " + jsonString;
    } catch (e) {
      if (DBG) console.error("NG> Incorrect JSON Format!!");
      jsonString = "NG> Incorrect JSON Format, please check it and try again.\n";
    }

    this.setState({
      webviewCmd: jsonString, 
      targetUrl: this.state.url + this.state.params
    });

    setTimeout(() => {
      this.refs.output.style.backgroundColor = "yellow";
      setTimeout(() => {
        this.refs.output.style.backgroundColor = "white";
      }, 200);
    }, 200);
  }

  render() {

    return (
      <div className="App">
        <AppBar position="static">
          <Toolbar>
            <IconButton color="inherit" aria-label="Back" style={{visibility: "hidden"}}>
              <Link to={`/`}>
                <ArrowBackIcon style={{ color: "white" }} />
              </Link>
            </IconButton>
            <Typography variant="h6" color="inherit" >
              Build Web Command
          </Typography>
          </Toolbar>
        </AppBar>
        <div style={{ marginTop: "10px" }}>
          <TextField
            style={{ margin: 10 }}
            id="outlined-cmpath"
            label="cmpath"
            value={this.state.cmpath}
            onChange={this.handleChange('cmpath')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            style={{ margin: 10 }}
            id="outlined-cmcode"
            label="cmcode"
            value={this.state.cmcode}
            onChange={this.handleChange('cmcode')}
            margin="normal"
            variant="outlined"
          />
          <TextField
            style={{ margin: 10 }}
            id="outlined-select-params"
            select
            label="params"
            value={this.state.params}
            onChange={this.handleChange('params')}
            SelectProps={{
              MenuProps: {
              },
            }}
            helperText="Choose a category"
            margin="normal"
            variant="outlined"
          >
            {currencies.map(option => (
              <MenuItem key={option} value={option}>
                {option}
              </MenuItem>
            ))}
            </TextField>
            <br/>
            <TextField
              id="outlined-full-width"
              label="url"
              value={this.state.url}
              onChange={this.handleChange('url')}
              style={{ width: "95%"}}
              helperText="The default URL for redirection without params"
              margin="normal"
              variant="outlined"
              InputLabelProps={{
                shrink: true,
              }}
            />
            <br/>
            <Button variant="contained" color="secondary" onClick={this.onButtonBuildClicked.bind(this)}>Build</Button><br/>
            <textarea 
              ref="output"
              rows="7" 
              cols="80" 
              style={{margin: 10}} 
              value={this.state.webviewCmd}
              onChange={this.handleChange('webviewCmd')}/><br/>
            <Button variant="contained" color="primary" onClick= {this.onButtonCheckUrlClicked.bind(this)}>Open URL<PageviewIcon/></Button>
            <br/><iframe src={this.state.targetUrl} 
                    style={{width: "343px", height: "549px"}}/>
          </div>
        </div>
    );
  }
}

export default WebCommand;


