import React, { Component } from 'react';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import GridListTileBar from '@material-ui/core/GridListTileBar';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import Default from "../Default";

import logo from '../logo.svg';
import '../App.css';


const tileData = [
  {
    img: "./icons/online-store.svg", 
    title: "Brand", 
    path: "/category"
  },
  {
    img: "./images/robot_part_1.png", 
  },
  {
    img: "./icons/bathroom.svg", 
    title: "Toilet"
  },
  {
    img: "./icons/support.svg", 
    title: "Voice"
  },
  {
    img: "./images/robot_part_2.png", 
  },
  {
    img: "./icons/game-controller.svg", 
    title: "Games", 
    path : "/game"
  },
  {
    img: "./icons/map-location.svg", 
    title: "Map"
  },
  {
    img: "./images/robot_part_3.png", 
  },
  {
    img: "./icons/stairs.svg", 
    title: "Floor"
  }
];

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    //backgroundColor: theme.palette.background.paper,
  },
  gridList: {
    width: "85%",
    height:"40%",
  },
};

class Home extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      redirectToReferrer : null, 
      sayingText: "", 
      selectedBrandInfo: null, 
      brandData: Default.getBrandData().slice(0,9)
    }
  }

  componentDidMount() {
    //this.startSpeechToText();
  }
  
  startSpeechToText() {

    var SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    var SpeechGrammarList = window.SpeechGrammarList || window.webkitSpeechGrammarList;
    var SpeechRecognitionEvent = window.SpeechRecognitionEvent || window.webkitSpeechRecognitionEvent;
    if(SpeechRecognition == null) {
      console.error("does not suport SpeechRecognition");
      return;
    }
    var recognition = new SpeechRecognition();
    // var speechRecognitionList = new SpeechGrammarList();
    // speechRecognitionList.addFromString(grammar, 1);
    // recognition.grammars = speechRecognitionList;
    //recognition.continuous = false;
    recognition.lang = 'zh-cn';
    recognition.interimResults = false;
    recognition.maxAlternatives = 1;
    recognition.start();
    console.log('Ready to receive a color command.');

    recognition.onresult = (event) => {
      // The SpeechRecognitionEvent results property returns a SpeechRecognitionResultList object
      // The SpeechRecognitionResultList object contains SpeechRecognitionResult objects.
      // It has a getter so it can be accessed like an array
      // The [last] returns the SpeechRecognitionResult at the last position.
      // Each SpeechRecognitionResult object contains SpeechRecognitionAlternative objects that contain individual results.
      // These also have getters so they can be accessed like arrays.
      // The [0] returns the SpeechRecognitionAlternative at position 0.
      // We then return the transcript property of the SpeechRecognitionAlternative object
    
      var last = event.results.length - 1;
      var text = event.results[last][0].transcript;
      this.setState({
        sayingText: text
      });
    
      console.log('Result received: ' + text + '.');
    }
    
    recognition.onspeechend = () => {
      //recognition.stop();
      console.log(">> onspeechend");
    }
    
    recognition.onnomatch = (event) => {
      console.log("onnomatch");
    }
    
    recognition.onerror = (event) => {
      console.error('Error occurred in recognition: ' + event.error);
    }

  }

  navigateToPath(path) {
    // this.setState({
    //   redirectToReferrer: path
    // });

    this.props.history.push(path);
  }

  navigateToPathWithBrandInfo(path, brandInfo) {

    this.props.history.push(
      {
        pathname: path,
        state: { brandInfo: brandInfo }
      }
    );
  }

  render() {

    // if (this.state.redirectToReferrer) {
    //   return (
    //       <Redirect 
    //           to={{
    //             pathname: this.state.redirectToReferrer,
    //             state: { brandInfo: this.state.selectedBrandInfo }
    //             }}/>
    //   )
    // }

      return (
        <div className="App">
        <header className="App-header">
          <GridList cellHeight={"25%"} style={styles.gridList} cols={3}>
          {tileData.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1}
              onClick={this.navigateToPath.bind(this, tile.path)}>
              <img src={tile.img} alt={tile.title} style={{height: "100%", width: "100%"}}/>
              <GridListTileBar style={{visibility: tile.title?"visible":"hidden"}}
                title={<h1>{tile.title}</h1>}
              />
            </GridListTile>
          ))}
          </GridList>
          <hr style={{width: "50%"}}></hr>
          <GridList cellHeight={"25%"} style={styles.gridList} cols={3}>
          {this.state.brandData.map(tile => (
            <GridListTile key={tile.img} cols={tile.cols || 1}>
              <img onClick={this.navigateToPathWithBrandInfo.bind(this, "/brand", tile)}
                src={tile.img} alt={tile.title} style={{height: "100%", width: "100%"}}/>
            </GridListTile>
          ))}
          </GridList>
        </header>
      </div>
      );
  }
}

  export default Home;


