import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import HomePage from "./Pages/Home";
import GameCenterPage from "./Pages/GameCenter";
import BrandDetailPage from "./Pages/BrandDetail";
import BrandNaviPage from "./Pages/BrandNavi";
import WebViewDebugPage from "./Pages/WebViewDebug";
import WebCommand from "./Pages/WebCommand";
import logo from './logo.svg';
import './App.css';

class App extends Component {
  
  state = { redirectToReferrer: false };
  
  RouteHomePage = (props) => {
    return (
      <HomePage
        {...props}
      />
    );
  }

  RouteGameCenterPage = (props) => {
    return (
      <GameCenterPage
        {...props}
      />
    );
  }

  RouteBrandNaviPage = (props) => {
    return (
      <BrandNaviPage
        {...props}
      />
    );
  }

  RouteBrandDetailPage = (props) => {
    return (
      <BrandDetailPage
        {...props}
      />
    );
  }

  RouteDebugPage = (props) => {
    return (
      <WebViewDebugPage
        {...props}
      />
    );
  }

  RouteWebCommandPage = (props) => {
    return (
      <WebCommand
        {...props}
      />
    );
  }

  render() {

    return (
      <Router>
        <div>
          <div id="child_area">
            <Route exact path="/" render={this.RouteHomePage} />
            <Route path="/home" render={this.RouteHomePage} />
            <Route path="/game" render={this.RouteGameCenterPage} />
            <Route path="/brand" render={this.RouteBrandDetailPage} />
            <Route path="/category" render={this.RouteBrandNaviPage} />
            <Route path="/debug" render={this.RouteDebugPage} />
            <Route path="/webcommand" render={this.RouteWebCommandPage} />
          </div>
        </div>
    </Router>
    );
  }
}

export default App;



