import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';

import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import logo from '../logo.svg';
import '../App.css';

class WebViewDebug extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      open: false,
      returnedValue: ""
    }
  }

  onButtonHideActionBarClicked(isShowed) {
    let isHidden = isShowed ? "false" : "true"
    window.androidJs.hideActionBar(isHidden);
  }

  onButtonGetDeviceIdClicked() {
    let result = window.androidJs.getDeviceId();
    this.setState({
      open: true,
      returnedValue: result
    });
  }

  onButtonGoBackClicked() {
    window.androidJs.callAndroid('generalBack');
  }

  onButtonGoToHomePageClicked() {
    window.androidJs.goToHomePage();
  }

  handleClose = () => {
    this.setState({ open: false });
  };

  render() {

    return (
      <div className="App">
        <AppBar position="static">
          <Toolbar>
            <IconButton color="inherit" aria-label="Back">
              <Link to={`/`}>
                <ArrowBackIcon style={{ color: "white" }} />
              </Link>
            </IconButton>
            <Typography variant="h6" color="inherit" >
              Debug
          </Typography>
          </Toolbar>
        </AppBar>
        <div style={{ marginTop: "10px" }}>
          <Button variant="contained" color="primary" onClick={this.onButtonHideActionBarClicked.bind(this, true)}>
            hideActionBar("true")
        </Button>
          <Button variant="contained" color="secondary" onClick={this.onButtonHideActionBarClicked.bind(this, false)}>
            hideActionBar("false")
        </Button>
          <Button variant="contained" onClick={this.onButtonGetDeviceIdClicked.bind(this)}>
            getDeviceId()
        </Button>
        <Button variant="contained" onClick={this.onButtonGoBackClicked.bind(this)}>
            callAndroid('generalBack');
        </Button>
        <Button variant="contained" onClick={this.onButtonGoToHomePageClicked.bind(this)}>
            goToHomePage();
        </Button>
        </div>
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{"return"}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">
              {this.state.returnedValue}
            </DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              OK
            </Button>
          </DialogActions>
        </Dialog>

      </div>
    );
  }
}

export default WebViewDebug;


