import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import {
    BrowserRouter as Router,
    Route,
    Link,
    Redirect,
    withRouter
  } from "react-router-dom";
import logo from '../logo.svg';
import '../App.css';

class BrandDetail extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
        redirectToReferrer: null
    }
  }

  goBack(e) {
    e.preventDefault();
    this.props.history.goBack();
  }

  render() {

      let brandInfo = this.props.location.state.brandInfo;
      console.log(">> brandInfo: ", this.props.location);
      return (
      <div className="App">
        <AppBar position="static">
          <Toolbar>
            <IconButton color="inherit" aria-label="Back" 
                        onClick={e => this.goBack(e)}>
                <ArrowBackIcon style={{color: "white"}}/>
            </IconButton>
            <Typography variant="h6" color="inherit" >
              Brand Detail
            </Typography>
          </Toolbar>
        </AppBar>
        <div style={{display: "flex", flexDirection: "column"}}>
          <div style={{flex: 1}}>
            <div style={{display: "flex"}}>
              <div style={{flex: "1"}}>
                <img src={brandInfo.img} style={{width: "100%", marginTop: "1px"}}/>
                <p style={{fontSize: "1rem"}}>Name: {brandInfo.title}</p>
                <p style={{fontSize: "1rem"}}>Tel: {brandInfo.phoneNumber}</p>
                <p style={{fontSize: "1rem"}}>Floor: {brandInfo.floor}</p>
              </div>
              <div style={{flex: "1"}}>
                <img src={brandInfo.picture} style={{width: "100%", height: "100%"}}/>
              </div>
            </div>
          </div>
          <div style={{flex: 1}}>
            <p style={{fontSize: "1.2rem", width: "100%", height: "100%"}}>{brandInfo.description}</p>
          </div>
          <div style={{flex: 1}}>
              <img src="./images/map.png" style={{width: "100%", height: "100%"}}/>
          </div>
        </div>
      </div>
      );
  }
}

  export default BrandDetail;


